/**
 * GLOBALS
 */
@import url("https://fonts.googleapis.com/css2?family=League+Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap");
* {
  margin: 0;
  padding: 0;
}

section {
  max-width: 1110px;
  margin: 0 auto;
}

h1 {
  font-family: "League Spartan", Arial, sans-serif;
  font-weight: 700;
  font-size: 200px;
  line-height: 200px;
  letter-spacing: -5px;
  color: #EEEFF4;
}

h2 {
  font-family: "League Spartan", Arial, sans-serif;
  font-weight: 600;
  font-size: 80px;
  line-height: 80px;
  letter-spacing: -2px;
  color: #1B1D23;
}

h3 {
  font-family: "League Spartan", Arial, sans-serif;
  font-weight: 600;
  font-size: 56px;
  line-height: 56px;
  letter-spacing: -2px;
  color: #1B1D23;
}

h4 {
  font-family: "League Spartan", Arial, sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  color: #1B1D23;
}

p {
  font-family: "League Spartan", Arial, sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;
  color: #60636D;
}

a {
  font-size: 15px;
  font-family: "League Spartan", Arial, sans-serif;
  text-decoration: none;
  cursor: pointer;
}

li {
  list-style: none;
}

.button {
  background: #1B1D23;
  padding: 24px 32px;
  cursor: pointer;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 24px;
}
.button p {
  color: white;
  margin: 0;
  line-height: 0;
}

.button:hover {
  background-color: #60636D;
  transition: 0.3s;
}

.header .header__container {
  max-width: 1110px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  padding: 56px 0;
}
.header .header__container .header__logo {
  margin-right: 95px;
}
.header .header__container .header__navlinks .navlinks__items {
  display: flex;
  flex-direction: row;
  padding: 0;
  gap: 56px;
}
.header .header__container .header__navlinks .navlinks__items .navlinks__item {
  list-style: none;
  color: #7D828F;
}
.header .header__container .header__navlinks .navlinks__items .navlinks__item a {
  font-weight: 700;
}
.header .header__container .mobile__header-navlinks {
  display: none;
}
.header .header__container .header__button {
  display: none;
}

@media only screen and (max-width: 768px) {
  .header .header__container {
    max-width: 311px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    padding: 32px 0;
    justify-content: space-between;
    position: relative;
  }
  .header .header__container .header__logo {
    margin-right: 0;
  }
  .header .header__container .header__navlinks {
    display: none;
  }
  .header .header__container .header__button {
    display: block;
  }
  .header .header__container .mobile__header-navlinks {
    display: none;
    max-width: 310px;
    z-index: 5;
  }
  .header .header__container .mobile__header-navlinks.responsive {
    display: block;
  }
  .header .header__container .mobile__header-navlinks .header-navlinks__wrapper {
    position: absolute;
    width: 100%;
    top: 96px;
    right: 0;
    box-shadow: -23px 42px 65px rgba(0, 0, 0, 0.25);
  }
  .header .header__container .mobile__header-navlinks .header-navlinks__wrapper .navlinks__items {
    background-color: #EEEFF4;
    padding: 48px;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
  .header .header__container .mobile__header-navlinks .header-navlinks__wrapper .navlinks__items a {
    font-size: 18px;
    font-weight: 700;
  }
}
.footer {
  margin-top: 100px;
}
.footer .footer__container-desktop {
  max-width: 1110px;
  margin: 0 auto;
  position: relative;
}
.footer .footer__container-desktop .footer-links {
  background-color: #EEEFF4;
  max-width: 984px;
  height: 200px;
  display: flex;
  align-items: center;
}
.footer .footer__container-desktop .footer-links .footer__logo {
  background-color: #1B1D23;
  width: 200px;
  text-align: center;
  height: 100%;
}
.footer .footer__container-desktop .footer-links .footer__logo a {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.footer .footer__container-desktop .footer-links .footer__navlinks .navlinks__items {
  display: flex;
  flex-direction: row;
  gap: 56px;
  margin-left: 85px;
}
.footer .footer__container-desktop .footer-links .footer__navlinks .navlinks__items .navlinks__item {
  list-style: none;
  color: #7D828F;
}
.footer .footer__container-desktop .footer-links .footer__navlinks .navlinks__items .navlinks__item a {
  font-weight: 700;
}
.footer .footer__container-desktop .footer__cta {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.footer .footer__container-mobile {
  display: none;
}

@media only screen and (max-width: 768px) {
  .footer {
    margin-top: 100px;
  }
  .footer .footer__container-desktop {
    display: none;
  }
  .footer .footer__container-mobile {
    display: block;
    width: 100%;
    position: relative;
  }
  .footer .footer__container-mobile .footer__logo {
    width: 120px;
    height: 120px;
    background-color: #1B1D23;
    position: absolute;
    top: -15%;
    left: 50%;
    transform: translateX(-50%);
  }
  .footer .footer__container-mobile .footer__logo a {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  .footer .footer__container-mobile .footer-links {
    background-color: #EEEFF4;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-bottom: 48px;
    padding-top: 100px;
  }
  .footer .footer__container-mobile .footer-links .navlinks__items {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
  .footer .footer__container-mobile .footer-links .navlinks__items a {
    font-weight: 700;
    color: #7D828F;
  }
  .footer .footer__container-mobile .footer-links .footer__cta {
    margin-top: 32px;
  }
}
.herobanner .herobanner__splide .splide__slide {
  background-image: url("/assets/home/desktop/herobanner1.png");
  height: 720px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 190px;
  align-items: flex-start;
  position: relative;
}
.herobanner .herobanner__splide .splide__slide .headings,
.herobanner .herobanner__splide .splide__slide .description,
.herobanner .herobanner__splide .splide__slide .button {
  max-width: 544px;
}
.herobanner .herobanner__splide .splide__slide h1 {
  font-weight: 700;
  font-size: 80px;
  line-height: 80px;
  letter-spacing: -2px;
  margin-bottom: 14px;
}
.herobanner .herobanner__splide .splide__slide p {
  color: white;
}
.herobanner .herobanner__splide .splide__slide .button {
  margin-top: 40px;
}
.banner .banner__container {
  position: relative;
}
.banner .banner__container .banner__image img {
  height: 100%;
  width: 100%;
}
.banner .banner__container .banner__image::after {
  content: "";
  background: #000000;
  opacity: 0.5;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 99.5%;
}
.banner .banner__container .banner__content {
  position: absolute;
  top: 50%;
  left: 20%;
  transform: translate(-20%, -50%);
  display: flex;
  flex-direction: column;
  gap: 26px;
}
.banner .banner__container .banner__content h3 {
  max-width: 445px;
  color: white;
}
.banner .banner__container .banner__content a {
  width: fit-content;
}

@media only screen and (max-width: 768px) {
  .banner .banner__container .banner__content {
    right: 32px;
    transform: translateX(-32px);
  }
  .banner .banner__container .banner__content h3 {
    font-size: 40px;
    line-height: 48px;
    letter-spacing: -1.42857px;
  }
}
.image-text {
  padding-top: 200px;
  padding-bottom: 100px;
}
.image-text .image-text__container {
  display: flex;
  position: relative;
  width: 100%;
  justify-content: space-between;
  align-items: flex-end;
}
.image-text .image-text__container .headings__container {
  position: absolute;
  top: -90px;
  left: 0;
}
.image-text .image-text__container .headings__container h1 {
  letter-spacing: 10px;
}
.image-text .image-text__container .texts__container {
  max-width: 446px;
  margin-left: 187px;
}
.image-text .image-text__container .texts__container h2 {
  margin-bottom: 56px;
  line-height: 56px;
  letter-spacing: -2px;
}
.image-text .image-text__container .image__container {
  height: 568px;
}
.image-text .image-text__container .image__container img {
  height: 100%;
}

@media only screen and (max-width: 768px) {
  .image-text {
    padding: 140px 32px 80px 32px;
  }
  .image-text .image-text__container {
    flex-direction: column;
    align-items: flex-start;
  }
  .image-text .image-text__container .headings__container {
    display: none;
  }
  .image-text .image-text__container .texts__container {
    margin-left: 0;
    position: relative;
    max-width: 100%;
  }
  .image-text .image-text__container .texts__container h2 {
    font-size: 40px;
    line-height: 48px;
    letter-spacing: -1.42857px;
  }
  .image-text .image-text__container .texts__container::before {
    content: "";
    width: 65px;
    height: 1px;
    background: #C8CCD8;
    position: absolute;
    top: -67px;
    left: 0;
  }
  .image-text .image-text__container .image__container {
    display: none;
  }
}